import { createSlice } from "@reduxjs/toolkit";
import type { PayloadAction } from "@reduxjs/toolkit";
import type { omponentsState,IRenameActions } from "./typeAndInterfaceomponentSlice/reduxInterface";

const initialState: omponentsState = {
  Namesomponents: ["1", "2", "3"],
};

const ComponentHandler = (
  Namesomponents: Array<string>,
  actionPayload: string,
  type: string,
  renameValue: string = ""
) => {
  return Namesomponents.map((el, i) => {
    if (el === actionPayload) {
      if (type === "delete") return Namesomponents.splice(i, 1);
      if (type === "rename") {
        Namesomponents[i] = renameValue
      }
    }
  });
};

export const omponentsSlice = createSlice({
  name: "omponent",
  initialState,
  reducers: {
    addomponent: (state, action: PayloadAction<string>) => {
      state.Namesomponents.push(action.payload);
    },
    deleteomponent: (state, action: PayloadAction<string>) => {
      ComponentHandler(state.Namesomponents, action.payload, "delete");
    },
    renameomponent: (state, action: PayloadAction<IRenameActions>) => {
      ComponentHandler(state.Namesomponents, action.payload.component, "rename", action.payload.renameValue);
    },
  },
});

export const { addomponent, deleteomponent,renameomponent } = omponentsSlice.actions;
export default omponentsSlice.reducer;
